import Vue from 'vue';
import VueRouter, { RouteConfig } from 'vue-router';
import {
  NotFound,
  Login,
  Farm,
  Main,
  Setting,
  Detail,
  UserSetting,
  CubeSetting,
  ProfileSetting,
} from './routes';

Vue.use(VueRouter);

const routes: Array<RouteConfig> = [
  {
    path: '/',
    component: Main,
    children: [
      {
        path: '',
        component: Farm,
      },
      {
        path: 'setting/:farm',
        component: Setting,
        children: [
          {
            name: 'CubeSetting',
            path: '/',
            component: CubeSetting,
          },
          {
            name: 'CubeSetting',
            path: 'cube',
            component: CubeSetting,
          },
          {
            name: 'UserSetting',
            path: 'user',
            component: UserSetting,
          },
          {
            name: 'ProfileSetting',
            path: 'profile',
            component: ProfileSetting,
          },
        ],
      },
      {
        path: 'detail/:farm',
        component: Detail,
      },
    ],
  },
  {
    path: '/login',
    component: Login,
  },
  {
    path: '/404',
    component: NotFound,
  },
  {
    path: '*',
    redirect: '/404',
  },
];

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
});

router.beforeEach((to, from, next) => {
  console.log(to, from, next);

  next();
});

export default router;
