




import { Component, Ref, Vue } from 'vue-property-decorator';
import * as am4core from '@amcharts/amcharts4/core';
import * as am4charts from '@amcharts/amcharts4/charts';
import am4langkoKR from '@amcharts/amcharts4/lang/ko_KR';
import am4themesAnimated from '@amcharts/amcharts4/themes/animated';

am4core.useTheme(am4themesAnimated);

@Component({
  name: 'Graph',
})
export default class Graph extends Vue {
  @Ref() chartdiv: HTMLDivElement|undefined;
  private chart: am4charts.XYChart|undefined;

  mounted() {
    const chart = am4core.create(this.chartdiv, am4charts.XYChart);
    chart.language.locale = am4langkoKR;

    chart.paddingRight = 20;

    chart.data = [
      {
        date: new Date('2020-10-01'),
        country: 'Lithuania',
        litres: 501,
      },
      {
        date: new Date('2020-10-02'),
        country: 'Czechia',
        litres: 301,
      },
      {
        date: new Date('2020-10-03'),
        country: 'Ireland',
        litres: 201,
      },
      {
        date: new Date('2020-10-04'),
        country: 'Germany',
        litres: 165,
      },
      {
        date: new Date('2020-10-05'),
        country: 'Australia',
        litres: 139,
      },
      {
        date: new Date('2020-10-06'),
        country: 'Austria',
        litres: 128,
      },
      {
        date: new Date('2020-10-07'),
        country: 'UK',
        litres: 99,
      },
      {
        date: new Date('2020-10-08'),
        country: 'Belgium',
        litres: 60,
      },
      {
        date: new Date('2020-10-09'),
        country: 'The Netherlands',
        litres: 50,
      },
    ];

    const dateAxis = chart.xAxes.push(new am4charts.DateAxis());

    const valueAxis = chart.yAxes.push(new am4charts.ValueAxis());
    valueAxis.title.text = 'Litres sold (M)';

    const series2 = chart.series.push(new am4charts.LineSeries());
    series2.name = 'Litres';
    series2.stroke = am4core.color('#CDA2AB');
    series2.strokeWidth = 3;
    series2.smoothing = 'monotoneX';
    series2.dataFields.dateX = 'date';
    series2.dataFields.valueY = 'litres';

    this.chart = chart;
  }

  beforeDestroy() {
    if (this.chart) {
      this.chart.dispose();
    }
  }
}
