



















































































import { Component, Prop, Vue } from 'vue-property-decorator';
import { Locales } from '@/types';

@Component({
  name: 'Main',
})
export default class Main extends Vue {
  get getLang() {
    return [
      {
        value: Locales.EN,
        caption: 'English',
      },
      {
        value: Locales.KR,
        caption: '',
      },
    ];
  }
}
